@import url(https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&display=swap);
ul {
    list-style-type: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}
  
.moodbar {
  padding: 1em;
  float: right;
  color: white;
  vertical-align:baseline;
}

#logOutUser {
  display: block;
  color: white;
  text-align: center;
  padding: 1em ;
  text-decoration: none;
}

.navbar_link {
  display: block;
  color: white;
  text-align: center;
  padding: 1em ;
  text-decoration: none;
  border-radius: 1em;
}

.navbar_link:hover:not(.active) {
  background-color: #111;
}

.active {
  background-color: #616161;
}
.h1, h1 {
    font-size: 2.5rem;
}

fieldset {
    min-width: 0;
    padding: 1rem 0;
    margin: 0;
    border: 0;
}

.form--login {
    display: inline-block;
    margin: 0 auto;
    text-align: left;
}

.form--login > fieldset > input[type="email"],
.form--login > fieldset > input[type="password"] {
    width: 25em;
}

.label--login {
    width: 8rem;
    display: inline-block;
}

.form-control {
    display: block;
    width: 94%;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.container--login {
    text-align: center;
}

.form--login {
    margin-top: 3em;
    background-position-x: right;
    background-image: url(/reactjournal/static/media/kunal-shinde--f0YLss50Bs-unsplash.82a1aada.jpg);
    z-index: 1;
    min-height: 25rem;
    min-width: 45rem;
    background-color: hsla(0,0%,100%,0.40);
    background-blend-mode: overlay;
    background-repeat: no-repeat;
}

.button--close {
    position: relative;
    bottom: -2rem;
}

.dialog {
    min-width: 15rem;
    min-height: 5rem;
}


